<template>
  <div class="main-game">
    <div class="main-game--header">
      <div class="main-game--header__left">
        <div class="main-game--header__left-title">
          <router-link to="/" class="small-logo">
            <img src="../../assets/img/logo-small.png" alt="" v-on:click="redirected" >
          </router-link>
          <h1>Огонь и Вода 4: в Хрустальном Храме (Fireboy and Watergirl)</h1>
        </div>
        <div class="pwa">
          <div class="pwa-icon">
            <img src="../../assets/img/small-pwa.svg" alt="">
          </div>
          <div class="pwa-title">
            <p>Install App</p>
          </div>
        </div>
      </div>
      <div class="main-game--header__right">
          <mq-layout mq="m+" class="pwaXL">
            <div class="pwaXL-icon">
              <img src="../../assets/img/small-pwa.svg" alt="">
            </div>
            <div class="pwaXL-title">
              <p>Install App</p>
            </div>
          </mq-layout>
        <button class="like">
          <img src="../../assets/img/like-small.svg" alt="">
        </button>
        <mq-layout mq="m+" class="num_likes">
          23.657
        </mq-layout>
        <button class="dislike">
          <img src="../../assets/img/dislike.svg" alt="">
        </button>
        <div class="rating">
          <div class="circle">8.6</div>
        </div>
        <button class="add_favs">
          <img src="../../assets/img/star-small.svg" alt="">
        </button>
        <button class="full_screen" v-fullscreen="fullOptions">
          <img src="../../assets/img/fullscreen.svg" alt="">
        </button>
      </div>
    </div>
    <div class="main-game--frame">
      <iframe v-if="play === true" src="https://html5.gamedistribution.com/27673bc45d2e4b27b7cd24e422f7c257/" frameborder="0"></iframe>
      <div v-else class="play" @click="showGame">
        Play
      </div>
    </div>
    <!-- <div class="pwa-button"><img src="../../assets/img/pwa-icon.svg" alt=""><div class="text">Install App</div></div> -->
  </div>
</template>

<script>

import { directive as fullscreen } from 'vue-fullscreen'
// import $ from 'jquery'

export default {
  data() {
    return {
      fs: false,
      play: false,
      fullscreen: false,
      teleport: false,
      fullOptions: {
        target: ".main-game",
        callback (isFullscreen) {
          this.fs = isFullscreen
        },
      },
    }
  },
  directives: {
    fullscreen
  },

  methods: {
    showGame(){
      this.play = true;
    },
    redirected(){
      this.$fullscreen.toggle()
    },
    toggleLogo(){
      console.log(this.fs)
      return this.fs
    },
    /* eslint-disable no-unused-vars */
    simulateClick (elem) {
      // Create our event (with options)
      let evt = new MouseEvent('click', {
          bubbles: true,
          cancelable: true,
          view: window
      });
      // If cancelled, don't dispatch our event
      let canceled = !elem.dispatchEvent(evt);
    },
    /* eslint-enable no-unused-vars */
  },
  mounted(){
    setTimeout(() => {
      var playBtn = document.querySelector('.play');
      this.simulateClick(playBtn);
      // console.log(this.$fullscreen.isFullscreen);
    }, 1000)
  }
  
}
</script>

<style lang="sass" scoped>

